import React from 'react';
import injectSheet from 'react-jss';
import Img from 'gatsby-image';

import BackgroundImageSection from '../components/BackgroundImageSection';
import Section from '../components/Section';
import Link from '../components/Link';
import Divider from '../components/Divider';

const styles = {
    sectionProducts: {
        marginBottom: '0.8rem',
        display: 'flex',
        justifyContent: 'center'
    },
    product: {
        display: 'inline-flex',
        flexDirection: 'column',
        alignItems: 'center',
        '& h3': {
            fontSize: '1.58rem',
            marginBottom: '1rem'
        }
    },
    links: {
        display: 'inline-flex',
        margin: '0 2.8rem'
    },
    sectionDemos: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center'
    }
};

export default injectSheet(styles)(({ classes, data }) => {
    const products = [
        {
            name: data.contents.downloads.products.client,
            links: [{
                resolutions: data.imageOculus.childImageSharp.resolutions,
                to: data.contents.downloads.products.urlOculus
            }, {
                resolutions: data.imageGoogleVR.childImageSharp.resolutions,
                to: data.contents.downloads.products.urlGoogleVR
            }]
        }, {
            name: data.contents.downloads.products.server,
            links: [{
                resolutions: data.imageUnity.childImageSharp.resolutions,
                to: data.contents.downloads.products.urlUnity
            }, {
                resolutions: data.imageUE4.childImageSharp.resolutions,
                to: data.contents.downloads.products.urlUE4
            }]
        }
    ].map((product, index) => {
        return (
            <div key={index} className={classes.product}>
                <h3>{product.name}</h3>
                <div className={classes.links}>
                    { product.links.map((link, index) => (
                        <Link key={index} to={link.to}>
                            <Img resolutions={link.resolutions} />
                        </Link>
                    ))}
                </div>
            </div>
        );
    });
    
    return (
        <div>
            <BackgroundImageSection
                sizesBackground={data.imageBackground.childImageSharp.sizes}>
                <h1>{data.contents.downloads.title}</h1>
                <p>{data.contents.downloads.description}</p>
            </BackgroundImageSection>
            <Section className={classes.sectionProducts}>
                {products}
            </Section>
            <Divider width="90%"/>
            <Section className={classes.sectionDemos}>
                <h3>{data.contents.downloads.demos.header}</h3>
            </Section>
        </div>
    );
});

export const query = graphql`
    query IndexDownloadsQuery {
        ...IndexLayoutFragment
        ...IndexDownloadsFragment
    }

    fragment IndexDownloadsFragment on RootQueryType {
        imageBackground: file(relativePath: { eq: "bg_downloads.jpg" }) {
            childImageSharp {
                sizes(maxWidth: 984) {
                    ...GatsbyImageSharpSizes
                }
            }
        }
    
        contents: stringsToml {
            downloads {
                title
                description
                products {
                    client
                    server
                    urlOculus
                    urlGoogleVR
                    urlUnity
                    urlUE4
                }
                demos {
                    header
                }
            }
        }
  
        imageOculus: file(relativePath: { eq: "download_oculus.png" }) {
            childImageSharp {
                resolutions(width: 150) {
                    ...GatsbyImageSharpResolutions
                }
            }
        }

        imageGoogleVR: file(relativePath: { eq: "download_googlevr.png" }) {
            childImageSharp {
                resolutions(width: 150) {
                    ...GatsbyImageSharpResolutions
                }
            }
        }

        imageUnity: file(relativePath: { eq: "download_unity.png" }) {
            childImageSharp {
                resolutions(width: 150) {
                    ...GatsbyImageSharpResolutions
                }
            }
        }

        imageUE4: file(relativePath: { eq: "download_ue4.png" }) {
            childImageSharp {
                resolutions(width: 150) {
                    ...GatsbyImageSharpResolutions
                }
            }
        }
    }
`;



